
body {
  margin: 0;
  padding: 0;
  padding-bottom: 50px;
}
.page-lang {
  position: absolute;
  top: 15px;
  right: 2%;
}
.page-header {
  box-sizing: border-box;
  padding: 90px 15px;
  width: 100%;
  height: 380px;
  color: #fff;
  text-align: center;
  background-color: #159957;
  background-image: linear-gradient(120deg, #155799, #159957);
}
@media only screen and (max-width: 958px) {
.page-header {
    height: 300px;
    padding: 60px 15px;
}
}
@media only screen and (max-width: 768px) {
.page-header {
    height: 370px;
    padding: 50px 15px;
}
}
.page-header .project-name {
  margin-top: 0;
  margin-bottom: 0.1rem;
  font-size: 2.25rem;
}
@media only screen and (max-width: 768px) {
.page-header .project-name {
    font-size: 25px;
}
}
.page-header .project-tagline {
  margin-bottom: 2rem;
  font-weight: normal;
  opacity: 0.7;
}
@media only screen and (max-width: 768px) {
.page-header .project-tagline {
    font-size: 16px;
}
}
.page-header .btn {
  padding: 0.6rem 0.9rem;
  font-size: 0.9rem;
  display: inline-block;
  margin-bottom: 1rem;
  color: rgba(255,255,255,0.7);
  background-color: rgba(255,255,255,0.08);
  border-color: rgba(255,255,255,0.2);
  border-style: solid;
  border-width: 1px;
  border-radius: 0.3rem;
  transition: color 0.2s, background-color 0.2s, border-color 0.2s;
  text-decoration: none;
  margin-left: 20px;
  box-sizing: border-box;
}
.page-header .btn:hover {
  color: rgba(255,255,255,0.8);
  text-decoration: none;
  background-color: rgba(255,255,255,0.2);
  border-color: rgba(255,255,255,0.3);
}
@media only screen and (max-width: 768px) {
.page-header .btn {
    display: block;
    width: 90%;
    padding: 0.75rem;
    font-size: 0.9rem;
    margin-left: 5%;
}
}
.item {
  width: 75%;
  margin-left: 12.5%;
}
@media only screen and (max-width: 1100px) {
.item {
    width: 85%;
    margin-left: 7.5%;
}
}
@media only screen and (max-width: 768px) {
.item {
    width: 92%;
    margin-left: 4%;
}
}
.item .item-header {
  margin-top: 3.5rem;
  margin-bottom: 2rem;
  font-weight: normal;
  color: #159957;
}
@media only screen and (max-width: 768px) {
.item .item-header {
    margin-top: 2rem;
    margin-bottom: 1rem;
}
}
.item .item-editor {
  width: 100%;
  height: 700px;
}
@media only screen and (max-width: 1600px) {
.item .item-editor {
    height: 500px;
}
}


